/**
 * @file Matrix transpose shader functions.
 */

export const glsl_transposeMat3 = `
mat3 transpose(mat3 m) {
  mat3 result;
  for (int i = 0; i < 3; ++i) {
    for (int j = 0; j < 3; ++j) {
      result[i][j] = m[j][i];
    }
  }
  return result;
}
`;



